/* NAVBAR */

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(228, 228, 228);
  position: fixed;
  top: 0%;
  width: 100%;
  height: 80px;
}

.logo {
  margin-left: 32px;
  font-size: 20px;
}

nav ul {
  display: flex;
  align-items: center;
  gap: 42px;
}

ul li {
  list-style: none;
  text-decoration: none;
}

li .nav-item {
  text-decoration: none;
  font-size: 21px;
  color: rgb(10, 10, 10);
}

.text {
  margin-top: 77px;
}

.cart {
  display: flex;
  align-items: center;
}

.cart p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 23px;
  text-align: center;
  background-color: black;
  color: white;
  position: relative;
  top: -17px;
  right: 12px;
  font-size: 15px;
  border-radius: 50%;
}

#bar {
  display: none;
}

.navbar #cross {
  display: none;
}

.active-nav #bar {
  display: none;
  transition: all 1s ease;
}

.icons-container {
  display: none;
}

@media screen and (max-width: 750px) {
  .navbar ul {
    display: none;
  }

  #bar {
    display: block;
  }

  .active-nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: black;
    width: 100%;
    height: 100vh;
    margin: auto;
    position: absolute;
    top: 0px;
    padding: 0px;
  }

  .active-nav .nav-item {
    color: white;
  }

  .icons-container {
    position: absolute;
    display: block;
    position: relative;
    left: -33px;
    font-size: 26px;
    cursor: pointer;
    color: white;
  }
}



/* MAIN PAGE */

.main{
    display: flex;
    gap: 33px;
    justify-content: center;
    margin-top: 152px;
}
.mainText{
    width: 440px;
}
.main img{
    width: 500px;
}
.mainText h1{
    font-size: 50px;
}
.mainText button{
    width: 140px;
    height: 40px;
    border: none;
    color: white;
    font-size: 20px;
    background-color: rgb(0, 0, 155);
    cursor: pointer;
}
@media screen and (max-width:1050px){
    .main{
        display: block;
        justify-content: center;
        text-align: center;
        
    }
    .main .mainText{
       margin: auto;
    }
}
@media screen and (max-width:580px){
    .main img{
        width: 90%;
        margin-top: 22px;
    }
    .mainText{
        width: 90%;
    }
    .mainText h1{
        font-size: 33px;
    }
}





/* SERIVCE */

.services{
    display: flex;
    flex-wrap: wrap;
    gap: 22px;
    justify-content: center;
    margin-top: 55px;
}
.serviceCard{
    width: 260px;
    background-color: #ebebeb;
    text-align: center;
    border-radius: 11px;
    padding: 12px;
    margin-top: 22px;
     
}
.serviceCard i{
    font-size: 32px;
    color: rgb(1, 67, 250);
}
.middleCard{
    display: block;
    text-align: center;
    justify-content: center;
}
.middleCard .serviceCard{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 11px;
    padding: 11px;
 

}
 
#side-icon{
    margin-top: 45px;
}
 
@media screen and (max-width:400px){
    .serviceCard{
        width: 72%;
    }
    .middleCard .serviceCard{
        width: 250px;
    }
    
    
     
}

@media screen and (max-width:325px){
    .middleCard .serviceCard{
        display: block;
        width: 200px;
    }
}



/* COMPANIES */

.company-container{
    text-align: center;
    padding: 22px;
    background-color: #dddbdb;
    margin-top: 66px;
}
.companyText p{
    font-size: 20px;
}
.companies{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 33px;
}
.companies img{
    width: 140px;
    height: 40px;
    margin-inline: 33px;
    margin-top: 22px;
}


/* CONTACT FORM */

.contact-page{
    margin-top: 116px;
    
}
.contact-text p{
    text-align: center;
    font-size: 33px;

}
.form{
    width: 400px;
    padding: 22px;
    margin: auto;
    background-color: rgb(245, 245, 245);
    text-align: center;
    margin-top: 85px;
     
}
.form input{
    width: 90%;
    font-size: 20px;
    padding: 11px;
    margin-top: 22px;
    background-color: transparent;
    border: 1px solid black;
    
}
.form textarea{
    margin-top: 22px;
    border: 1px solid black;
    font-size: 18px;
    width: 95%;
}
.form button{
    margin-top: 22px;
    width: 120px;
    height: 37px;
    border: none;
    font-size: 20px;
    color: white;
    background-color: rgb(0, 89, 255);
    cursor: pointer;
}
 






/* FOOTER */
.footer-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px;
    width: 700px;
    height: 60px;
    background-color: rgb(243, 243, 243);
    border-radius: 8px;
    margin: auto;
    position: relative;
    top: 125px;
    

}
.footer-top button{
    width: 140px;
    height: 40px;
    background-color: rgb(3, 3, 192);
    color: white;
    font-size: 17px;
    border: none;
}
.footer{
    display: flex;
    justify-content: space-around;
    background-color: black;
    color: white;
    padding: 66px;
    margin-top: 66px;
}
.footer input{
    padding: 6px;
    font-size: 18px;
    border: none;
    outline: none;

}
.footer button{
    background-color: blue;
    color: white;
    font-size: 18px;
    border: none;
    margin-top: 22px;
    width: 100px;
    height: 34px;
    cursor: pointer;
}
.footer i{
    margin-inline: 11px;
    font-size: 22px;
    border: 1px solid white;
    padding: 6px;
    border-radius: 50%;
}
.footer .head{
    font-size: 20px;
}
.bottom-footer{
    display: flex;
    justify-content: space-around;
    border-top: 1px solid white;
    background-color: black;
    color: white;
    padding: 22px;
}
.footer-btn{
    color: white;
    text-decoration: none;
}














/* ERROR PAGE CSS */

.error-page{
    text-align: center;
    margin-top: 225px;
}
.error-page .error-num{
    font-size: 99px;
}
.error-page p{
    font-size: 32px;
    margin-top: -84px;
}
.error-page button{
    width: 140px;
    height: 40px;
    border-radius: 4px;
    background-color: rgb(0, 0, 219);
    border: none;
}
.error-page .back-btn{
    color: white;
    text-decoration: none;
    font-size: 18px;
}